import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import Backend from 'i18next-http-backend';
import LanguageDetector from 'i18next-browser-languagedetector';

i18n
  .use(Backend)
  .use(LanguageDetector)
  .use(initReactI18next)
  .init({
    fallbackLng: 'en',
    debug: process.env.NODE_ENV === 'development', // تفعيل وضع التصحيح فقط في التطوير
    interpolation: {
      escapeValue: false, // مهم لاستخدام HTML في الترجمات
    },
    backend: {
       loadPath: '/locales/{{lng}}/{{ns}}.json',
      // loadPath: '/locales/{{ar}}/{{translation}}.json', // المسار المخصص لملفات الترجمات
    },
  });

export default i18n;